<template>
  <v-app id="app">
    <div id="routerLink">
      <v-card>
        <v-tabs background-color="primary" dark>
          <v-tab to="/">Home</v-tab>
          <v-tab to="/yunjiTest">YunjiTestPage</v-tab>
          <v-tab to="/hyoriTest">HyoriTestPage</v-tab>
          <v-tab to="/YujinTest">YujinTestPage</v-tab>
          <v-tab to="/DongminTest">DongminTestPage</v-tab>
        </v-tabs>
      </v-card>
      <router-view />

        <div id="app1">
          <p>Vue with axios2</p>
          <v-btn v-on:click="getData">getData(View Console)</v-btn>
        </div>
    </div>
  </v-app>
</template>

<script src="https://cdn.jsdelivr.net/npm/vue@2.5.2/dist/vue.js"></script>
<script src ="https://unpkg.com/axios/dist/axios.min.js"></script>

<script>

import axios from 'axios'
export default {
  name: "app1",
  methods: {
    getData: function() {
      axios.get(
        "/api/employee"
      ).then(function(response) {
        console.log(response);
      });
    }
  }
};

</script>

<style>
*:not(button) {
  width: 100%;
}
</style>



